*{
  font-family: Arial, Helvetica, sans-serif;
}
body{
  padding:  60px;
}
input{
  font-size: 1.3rem;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 15px;;
  margin-bottom: 10px;
  margin-right: 10px;
  max-width: 260px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
  width: 100%;
}
.card img{
  width: 100%;
  height: 180px;
}

